<template>
  <div
    class="display"
    :style="{
      opacity: opacity,
      '--background-image': background.image,
      '--background-color': background.color
    }"
  >
    <router-view></router-view>
  </div>
</template>
<script>
export default {
  name: "Display",
  components: {},
  data() {
    return {
      opacity: "0%"
    };
  },
  props: {
    activationState: Boolean
  },
  computed: {
    background() {
      let image = this.$store.getters.backgroundImage;
      let color = this.$store.getters.backgroundColor;
      return {
        image: image,
        color: color
      };
    }
  },
  watch: {
    activationState: function(val) {
      this.activation(val);
    }
  },
  methods: {
    activation(state) {
      state ? (this.opacity = "100%") : (this.opacity = "0%");
    }
  },
  created() {
    this.activation(this.activationState);
  }
};
</script>
<style scoped lang="scss">
@import "../assets/scss/var";
@import "../assets/scss/mixins";

.display {
  --background-image: url("#");
  --background-color: #000000;
  user-select: none;
  transition: linear opacity 0.2s;
  background-image: var(--background-image);
  background-color: var(--background-color);
  background-size: cover;
}
</style>
